@use "uswds-core" as *;

@font-face {
      font-family: 'source-sans-pro-web-normal-400-fallback';
      size-adjust: 94%;
      src: local('Arial');
}


img { height: auto;}

body>header { min-height: 220px;
   @include at-media-max($theme-header-min-width) {
      min-height: 48px;
   }

  
}

.grid-container.hero-wrapper { padding: 0;}